:root{
    --fuenteHeading: 'PT Sans', sans-serif;
    --fuenteParrafos: 'Open Sans', sans-serif;

    --colorNegro: #000000;
    --colorGris: #757575;
    --colorBlanco: #ffffff;
    --grisclaro: #DFE9F3;
}


html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}

body {
    font-family: var(--fuenteParrafos);
    font-size: 1.6rem;
    line-height: 2;
    background-image: linear-gradient(to top, var(--grisclaro) 0%, var(--colorBlanco) 100%);
}

a {
    text-decoration: none;
}

/* a:hover {
    color: var(--colorPrimario);
}
