h1, h2, h3, h4 {
    font-family: var(--fuenteHeading);
    line-height: 1.2;
}

h1 {
    font-size: 4.8rem;
}

img {
    max-width: 100%;
    border: none;
}

.contenedor {
    max-width: 125rem;
    width: 90%; 
    margin: 0 auto;
}

.no-margin {
    margin: 0;
}


